import React, { Component } from 'react';
import './ColorTool.css';

//SVG and PNG imports
import { ReactComponent as MainBack } from '../img/CT_Main.svg';
import { ReactComponent as BagBack } from '../img/CT_Bag_Back.svg';
import MainPNG from '../img/CT_Main.png';
import BagPNG from '../img/CT_Bag.png';

//import the Color Package to map Color Keys 
import ColorPackage from './colorData.js';

//States for Interacting with the Tool
let pickerState = 0; //0 = Bag/Background | 1 = Bag | 2 = Background  
let idPrimaryHex = document.getElementById("Primary-Hex");

export class ColorTool extends Component {

    changeScheme(e, id) {
        if (e !== pickerState) {
            document.getElementById("Primary-Scheme-Container").classList.remove("selected");
            document.getElementById("Secondary-Scheme-Container").classList.remove("selected");

            pickerState = e;
            console.log("new State set to:" + pickerState);
            document.getElementById(id).classList.add("selected");
        } else {
            pickerState = 0;
            console.log("State has been reset to:" + pickerState);
            document.getElementById("Primary-Scheme-Container").classList.remove("selected");
            document.getElementById("Secondary-Scheme-Container").classList.remove("selected");
        }
    }

    changeColor(i) {
        if (1 === pickerState) { // Bag
            document.getElementById("BSVG").style.fill = i;
            document.getElementById("Primary-Scheme-Color-Box").style.backgroundColor = i;
            document.getElementById("Primary-Hex").innerHTML = i;
        } else if (2 === pickerState) { // Background
            document.getElementById("ColorTool").style.backgroundColor = i;
            document.getElementById("Secondary-Scheme-Color-Box").style.backgroundColor = i;
            document.getElementById("Secondary-Hex").innerHTML = i;
        } else if (0 === pickerState) { // both 
            document.getElementById("ColorTool").style.backgroundColor = i;
            document.getElementById("BSVG").style.fill = i;
            //
            document.getElementById("Primary-Scheme-Color-Box").style.backgroundColor = i;
            document.getElementById("Primary-Hex").innerHTML = i;
            document.getElementById("Secondary-Scheme-Color-Box").style.backgroundColor = i;
            document.getElementById("Secondary-Hex").innerHTML = i;
        } else {
            console.log("Error encountered! Please reload page!")
        }
    }

    render() {
        return (
            <div id="ColorTool">
                <div className="ColorTool-Container"> {/* Image Space */}

                    <div className="Canvas-Container" >
                        <MainBack id="SVG" className="Layer" style={{ fill: 'rgba(0, 0, 0, 0)' }} />
                        <img src={MainPNG} className="Layer" id="PNG" alt=""></img>
                        <BagBack id="BSVG" className="Layer" style={{ fill: "#fff" }} />
                        <img src={BagPNG} className="Layer" id="BPNG" alt=""></img>
                    </div>


                    <div className="Color-Tabs-Controller"> {/* Map For Collor Tabs */}

                        <div className="Grid Scroll">
                            {ColorPackage.map(({ color, idKey }) => {
                                return <div
                                    className="Tab"
                                    key={idKey}
                                    id={idKey}
                                    onClick={() => this.changeColor(color)}
                                    style={{ backgroundColor: color }}
                                ></div>
                            })}
                        </div>

                        <div id="Scheme-Controller"> {/* Prim and Secondary Buttons */}

                            <div
                                className="Scheme-Container"
                                id="Primary-Scheme-Container"
                                onClick={() => this.changeScheme(1, "Primary-Scheme-Container")}
                                style={{ backgroundColor: "white" }}
                            >
                                <div className="Scheme-Title">
                                    <p id="Primary-Scheme-Title">Primary</p>
                                </div>

                                <div id="Primary-Scheme-Color-Box" className="Scheme-Color-Box">
                                    <div className="Scheme-Hex">
                                        <p id="Primary-Hex">#FFFFFF</p>
                                    </div>
                                </div>
                            </div>

                            <div
                                className="Scheme-Container"
                                id="Secondary-Scheme-Container"
                                onClick={() => this.changeScheme(2, "Secondary-Scheme-Container")}
                                style={{ backgroundColor: "white" }}
                            >
                                <div className="Scheme-Title">
                                    <p id="Secondary-Scheme-Title">Secondary</p>
                                </div>

                                <div id="Secondary-Scheme-Color-Box" className="Scheme-Color-Box">
                                    <div className="Scheme-Hex">
                                        <p id="Secondary-Hex">#FFFFFF</p>
                                    </div>
                                </div>
                            </div>

                        </div>

                    </div>


                </div>
            </div>
        )
    }
}

{/* */ }