import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Navigation.css';

export class Navigation extends Component {

    changeColor(i, a, b) {
        document.getElementById(a).style.color = "#fff";
        document.getElementById(b).style.color = "#fff";
        document.getElementById(i).style.color = "#fdd835";
    }

    render() {
        return (
            <div id="Navigation">
                <div className="Navigation-Bar Nav-Size">

                    <div className="Logo">
                        <h1>FAB</h1>
                    </div>
                    <div className="Spacer">

                    </div>
                    <div className="Nav">
                        <Link to="/" className="NavButton" onClick={() => this.changeColor("Home", "Color_Tool", "Usage")}>
                            <p id="Home" className="ButtonText">Home</p>
                        </Link>
                        <Link to="/color_tool" className="NavButton" onClick={() => this.changeColor( "Color_Tool","Home", "Usage")}>
                            <p id="Color_Tool" className="ButtonText">Color_Tool</p>
                        </Link>
                        <Link to="/usage" className="NavButton" onClick={() => this.changeColor("Usage","Home", "Color_Tool")}>
                            <p id="Usage" className="ButtonText">Usage</p>
                        </Link>
                    </div>
                </div>
                <div className="Top-Spacer Nav-Size">{/*   Creats a Spacing when you reach the top, so the content below hide behind the bar  */}</div>  
            </div>





        )
    }
}